.header{
  padding: .625em 0;
  background-color: $primary;
  &__inner{
    display: flex;
    align-items: center;
  }
  &__logo{
    display: block;
    flex-shrink: 0;
    width: 8.25em;
    height: 8.25em;
    position: relative;
    margin-right: 1.375em;
    img{
      pointer-events: none;
      @include centerAbsolute;
      width: 220%;
      display: block;
    }
  }
  &._open{
    .menu{
      opacity: 1;
      pointer-events: unset;
    }
    .burger{
      ._view1{
        opacity: 0;
      }
      ._view2{
        opacity: 1;
      }
    }
  }
}

.burger{
  display: none;
}

.menu{
  margin-left: auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
  &__title{
    font-size: 1.875em;
    line-height: 90%;
    text-transform: uppercase;
    font-weight: 700;
    font-family: $font2;
    letter-spacing: -.05em;
  }
  &__desc{
    max-width: 34.75em;
    margin-top: 1.25em;
    color: #c8c8c8;
    font-size: .9375em;
    line-height: 130%;
    &-row{
      font-weight: 700;
      text-transform: uppercase;
      letter-spacing: 0.007em;
    }
    &-row2{
      line-height: 120%;
      letter-spacing: 0.007em;
    }
  }
}

._full{
  grid-column: 1/-1;
}

.menu-contacts{
  display: grid;
  grid-template-columns: auto auto;
  gap: 1em;
  width: 20.5em;
  max-width: 100%;
}

.contact-item{
  font-weight: 300;
  color: #1f1f1f;
  display: flex;
  &__icon{
    height: 1.25em;
    width: .875em;
    @include center;
    margin-right: .5em;
    flex-shrink: 0;
    img{
      width: 100%;
    }
  }
  &__text{
    font-size: 1em;
    line-height: 125%;
  }
  a{
    white-space: nowrap;
    display: block;
    line-height: inherit;
    &:hover{
      color: $white;
    }
  }
}

.nav{
  padding: 1em 0;
  border-bottom: .375em solid $primary;
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-left: 9.625em;
    padding-right: 8.25em;
  }
  &__link{
    display: flex;
    align-items: center;
    font-weight: 900;
    text-transform: uppercase;
    span{
      font-size: 1em;
      line-height: 125%;
      letter-spacing: 0.2em;
    }
    img{
      margin-right: .5625em;
      height: .75em;
    }
  }
}


@media screen and (max-width: 992px){
  .header{
    &__logo{
      width: 5em;
      height: 5em;
    }
  }

  .burger{
    margin-left: auto;
    z-index: 11;
    position: relative;
    width: 1.5em;
    height: 1.5em;
    display: flex;
    img{
      @include centerAbsolute;
      width: 100%;
      transition: inherit;
    }
    ._view2{
      opacity: 0;
      width: 90%;
    }
  }

  .menu{
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    background-color: $white;
    padding: 4.75em 1em 2em;
    z-index: 10;
    overflow-y: auto;
    display: block;
    &__text{
      max-width: 21em;
    }
    &__title{
      line-height: 120%;
      font-size: 1.5em;
    }
    &__desc{
      display: none;
    }
  }

  .menu-contacts{
    margin-top: 2em;
    br{
      display: none;
    }
  }

  .nav{
    &__inner{
      padding: 0;
    }
  }
}


@media screen and (max-width: 576px){
  .header{
    &__logo{
      width: 4em;
      height: 4em;
      margin-right: .5em;
    }
  }

  .menu{
    &__text{
      font-size: .75em;
    }
    &__title{
      line-height: 120%;
      font-size: 1.5em;
    }
  }

  .menu-contacts{
    grid-template-columns: 100%;
  }

  .nav{
    border-width: .25em;
    &__link{
      span{
        font-size: .6875em;
      }
    }
  }
}
