@import "../vars.scss";
.breadcrumbs {
    margin-top: -.25em;
    li{
        position: relative;
        padding-left: .75em;
        display: inline;
        font-size: .875em;
        letter-spacing: 0.1em;
        color: #646464;
        margin-left: .25em;
        &:after{
            display: none;
        }
        &:after,
        &:before {
            content: '/';
            position: absolute;
            z-index: 1;
            left: 0;
            top: .25em;
            color: $primary;
        }
        &:first-child{
            margin: 0;
        }
        &:last-child{
            color: #fff;
        }
        &:first-child:last-child{
            color: #646464;
            &:after{
                display: unset;
                left: 100%;
                margin-left: .25em;
            }
        }
    }
}
