.contacts{
  padding: 2em 0 1em;
}

.contacts-form{
  width: 50.75em;
  max-width: 100%;
  margin: 0 auto;
  &__grid{
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .625em));
    gap: 1.25em;
  }
  &__bottom{
    width: 24.75em;
    max-width: 100%;
    margin: 1.25em auto 0;
  }
  &__btn{
    width: 100%;
    img{
      width: 1.125em;
      margin-right: .25em;
    }
  }
  &__hint{
    margin-top: .75em;
  }
}


@media screen and (max-width: 992px){

}


@media screen and (max-width: 576px){
  .contacts-form{
    &__grid{
      grid-template-columns: 100%;
      gap: 1em;
    }
    &__bottom{
      margin-top: 1em;
    }
  }
}
