.portfolio{
  padding: 3em 0 8.5em;
  &__items{
    @include grid100gap(2.5em);
    padding: 0 2.25em;
  }
}

.portfolio-card,
.portfolio-info{
  &__title,
  &__city{
    font-size: .8125em;
    line-height: 140%;
    letter-spacing: -0.002em;
  }
  &__title{
    text-transform: uppercase;
  }
}

.portfolio-card{
  display: flex;
  align-items: center;
  max-width: 58.75em;
  &:hover{
    color: $black;
  }
  &__img{
    flex-shrink: 0;
    margin-right: 1em;
    width: 6.375em;
    height: 6.375em;
    border-radius: 50%;
    border: 2px solid $primary;
    img{
      width: 100%;
      height: 100%;
      display: block;
      object-fit: cover;
      border-radius: inherit;
    }
  }
  &__desc{
    margin-top: .5em;
    padding-top: .75em;
    position: relative;
    &:before{
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 12.75em;
      height: 2px;
      background-color: $primary;
    }
  }
}


@media screen and (max-width: 992px){
  .portfolio{
    padding: 3em 0;
    &__items{
      padding: 0;
    }
  }
}


@media screen and (max-width: 576px){
  .portfolio{
    &__items{
      gap: 1.5em;
    }
  }

  .portfolio-card{
    display: block;
    padding: 1em;
    border: 1px solid $primary;
    &__img{
      margin: 0 0 1em;
    }
  }
}
