.portfolio-item{
  padding-bottom: 4em;
  margin-top: -.5em;
  &__img{
    width: 100%;
    display: block;
  }

  @media screen and (min-width: 993px){
    .container{
      padding: 0 12em;
    }
  }
}


.portfolio-info{
  padding: 1.25em 0 1.75em;
  &__img{
    margin-top: 1.5em;
    position: relative;
    &:before{
      content: '';
      position: absolute;
      left: 0;
      bottom: 2.5em;
      width: 100%;
      height: 2px;
      background-color: $primary;
    }
    img{
      width: 100%;
      display: block;
      max-width: 25em;
    }
  }
  &__desc{
    margin-top: .625em;
  }
}

.scheme{
  padding: .625em 0;
  background-color: #9d9d9c;
  &__items{
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 2em / 3));
    gap: 1em;
  }
}

.scheme-card{
  display: block;
  position: relative;
  &__img{
    @include imgCover(69.2%);
  }
  &__btn{
    position: absolute;
    right: 0;
    bottom: 0;
    img{
      display: block;
      width: 1.75em;
    }
  }
}

.portfolio-info2{
  display: grid;
  grid-template-columns: 50% calc(50% - 1em);
  gap: 1em;
  margin-top: 1.25em;
  &__col{
    padding-bottom: .5em;
    padding-top: .5em;
    border-bottom: 2px solid $primary;
  }
  &__img{
    width: 100%;
    display: block;
  }
}

.portfolio-info3{
  margin-top: 2em;
}

.text{
  p{
    font-size: .875em;
    line-height: 120%;
    margin-bottom: 1.5em;
    &:last-child{
      margin: 0;
    }
  }
  img{
    width: 100%;
    display: block;
  }
  hr{
    margin: 1.5em 0 1em;
    display: block;
    height: 2px;
    border: none;
    background-color: $primary;
  }
}


@media screen and (max-width: 992px){
  .portfolio-item{
    padding-bottom: 3em;
    margin-top: 0;
  }
}


@media screen and (max-width: 576px){
  .scheme{
    &__items{
      grid-template-columns: 100%;
      gap: .5em;
    }
  }

  .portfolio-info2{
    grid-template-columns: 100%;
  }
}

