.page-top{
  position: relative;
  z-index: 2;
  background-image: url(../img/bg.png);
  background-repeat: repeat-x;
  background-size: auto 100%;
  color: $white;
  &__inner{
    padding-left: 9.625em;
    padding-right: 6em;
    padding-bottom: .25em;
    min-height: 3.375em;
    display: flex;
    align-items: center;
  }
  &__links{
    display: flex;
    justify-content: space-between;
    width: 100%;
  }
  &__link{
    display: flex;
    align-items: center;
    justify-content: space-between;
    span{
      font-size: .875em;
      letter-spacing: 0.1em;
    }
    @media screen and (min-width: 993px){
      &:nth-child(2){
        flex-grow: 1;
        padding: 0 2.625em;
        &:before,
        &:after{
          content: '';
          flex-shrink: 0;
          height: 1.5em;
          width: 2px;
          background-color: #854139;
        }
      }
    }
  }
}

.catalog{
  padding: 1.25em 0 .875em;
  &__items{
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 2em));
    gap: 2em 3em;
  }
}

.catalog-card2{
  &__img{
    img{
      width: 100%;
      display: block;
    }
  }
}

.catalog-card{
  position: relative;
  min-height: 21.5em;
  padding-top: .75em;
  &__bg{
    display: block;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
  }
  &__img{
    display: flex;
    justify-content: center;
    padding: 0 3em;
    img{
      max-width: 100%;
      display: block;
    }
  }
  &__desc{
    margin-top: -3em;
    padding-left: 2.5em;
    padding-right: 2.25em;
    transform: translateX(2%);
    span{
      display: block;
    }
    @for $i from 1 through 100 {
      span:nth-child(#{$i}) {
        transform: translateX(-1 * ($i * 1.5%));
      }
    }
  }
  &__top{
    position: absolute;
    display: flex;
    top: .75em;
    left: 1.5em;
    width: 95%;
  }
  &__num{
    width: 1.25em;
    height: 1.25em;
    color: $white;
    @include center;
    border-radius: 50%;
    background-color: $primary;
    margin-right: .25em;
  }
  &__title{
    padding-top: .375em;
    text-transform: uppercase;
    font-size: .8125em;
    letter-spacing: 0.1em;
  }
  &:nth-child(2){
    .catalog-card__img{
      margin-top: -2em;
    }
    .catalog-card__desc{
      margin-top: -2em;
    }
  }
  &:nth-child(4){
    .catalog-card__desc{
      margin-top: -2em;
    }
  }
  &:last-child{
    .catalog-card__img{
      margin-top: -2em;
    }
    .catalog-card__desc{
      margin-top: -2em;
    }
  }
}


@media screen and (max-width: 992px){
  .page-top{
    background-color: #1e2843;
    &__inner{
      padding: 1em 0;
    }
    &__links{
      flex-direction: column;
      align-items: flex-start;
    }
    &__link{
      margin: 0 0 1em;
      &:last-child{
        margin: 0;
      }
    }
  }

  .catalog{
    &__items{
      grid-template-columns: repeat(2,calc(50% - 1em));
      gap: 2em;
    }
  }

  .catalog-card{
    &__desc{
      margin-top: -2em;
    }
    &:nth-child(3){
      .catalog-card__desc{
        margin-top: -3em;
      }
    }
    &:nth-child(4){
      .catalog-card__desc{
        margin-top: -1em;
      }
    }
    &:last-child{
      .catalog-card__desc{
        margin-top: -2em;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .page-top{
    &__link{
      span{
        font-size: .75em;
      }
    }
  }

  .catalog{
    &__items{
      grid-template-columns: 100%;
    }
  }

  .catalog-card{
    &__desc{
      .text13{
        font-size: .75em;
      }
    }
  }
}
