.footer{
  background-color: #1e2843;
  color: $primary;
  &__inner{
    padding: .5em 0;
    min-height: 2.5em;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__by,
  &__c{
    font-size: .875em;
    font-weight: 300;
    a{
      text-decoration: underline;
    }
  }
}


@media screen and (max-width: 992px){

}


@media screen and (max-width: 576px){
  .footer{
    &__inner{
      display: block;
      padding: 1em 0;
    }
    &__by,
    &__c{
      font-size: .75em;
    }
    &__c{
      margin-top: 1em;
    }
  }
}
