@import "../vars";
.m-btn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0 2em;
    height: 3.125em;
    font-weight: 500;
    span{
        font-size: 1.25em;
    }
    &:focus {
        box-shadow: none;
    }
}

.m-btn-orange{
    background-color: $primary;
    color: $white;
    &:hover{
        background-color: darken($primary,10%);
        color: $white;
    }
}
